.destacar {
  background-color: #FFAFAF; /* Cor de fundo vermelha */
}


.cadeira-card {
    border-radius: 8px;
   border: 1px solid #C3C3C3;
    box-shadow: 0 2px 7px 0 rgba(0,0,0, 0.15);
   /* display: flex;*/
    padding: 15px;
    align-items: flex-start;
    background: #ffffff;
    margin-bottom: 10px;
  }
  .title_turma_professor{
    padding-left: 3%;
    color: blue;
  }
  .title_turma{
    padding-left: 20%;
    color: blue;
  }
.turma{
  padding: 2%;
  color: #f27474;
}
  
  .cadeira-card__semestre {
    float: right;
    margin-right: 10px;
    color: blue;
  }
  
  .cadeira-card__titulo {
    font-size: 16px;
    color: #2C3E50;
  }
  
  .cadeira-card__codigo {
    
    color: #3498DB;
  }
  .cadeira-card__titulo{
    font-size: 15px;
    font-weight: 200;
    color: coral;

  }
  .cadeira-card__footer {
    display: flex;
    align-items: center;
  }
  
  .cadeira-card__comment {
    color: #95A5A6;
    font-weight: 700;
  }

  .cadeira-card__pauta{
    border: 1px solid #3498DB;
    border-radius: 4px;
    color: #3498DB;
    padding: 8px 10px;
    margin-right: 6px;
    text-decoration: none;  
  }
 
  .cadeira-card__comments-count {
    margin-left: auto;
    margin-right: 20px;
    color: #3498DB;
  }
  
  .cadeira-card__link {
    border: 1px solid #3498DB;
    border-radius: 4px;
    color: #3498DB;
    text-decoration: none;
    padding: 8px 10px;
  }